.glass{
    background: rgba(255, 255, 255, 0.55);
    border-radius: 16px;
    box-shadow: 0 4px 30px #4747470b;
    backdrop-filter: blur(7.1px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    @apply border-4 border-gray-50 shrink-0 h-3/4 w-[30%] rounded-3xl py-20 px-7 min-w-max;
    color: #fff;
   background-color: rgba(77, 76, 76, 0.55);
   border-color: rgba(105, 104, 104, 0.655);

}

.profile_img{
    @apply border-4 border-gray-100 w-[135px] rounded-full shadow-lg cursor-pointer;
    @apply hover:border-gray-200;
}

.textbox,.textarea{
    @apply border-0 px-5 py-4 rounded-xl  shadow-sm text-lg;
    @apply focus:outline-none;
    color: #222;
    z-index: 100000;
}
.textarea{
    width: 250px;
}

.btn{
    @apply border bg-indigo-500  py-4 w-3/4 rounded-lg text-gray-50 text-xl shadow-sm text-center;
    background-color:  #ba47dd;
    width: 260px;
}

.btn:hover{
    background-color: #ff6a6a;
}

input[type='file']{
    display: none;
}



@media (min-width:320px) and (max-width:375px){
    .glass{
       
        width: 95%;
        @apply  w-[95%] min-w-min;
        padding: 12px 5px;
    }
    .address{
        /* width: 370px; */
    }
    .textbox{
        width: 90%;
    }
    /* .btn{
        width: 90%;
    } */
}

